import moment from 'moment';
export default {
  name: 'promotions',
  data: function data() {
    return {
      promoList: []
    };
  },
  created: function created() {
    this.getPromoList();
  },
  methods: {
    getPromoList: function getPromoList() {
      var _this = this;

      var url = '/_promo/promo_list_new.txt';
      this.$api.getWmsStatic(url).then(function (_ref) {
        var data = _ref.data;
        var promo;
        var list = [];
        var nowTime = Date.now();

